<template>
  <div class="orderAll">
    <!-- 订单空状态 -->
    <div class="Emptystate" v-if="this.lists == ''">
      <div class="o-t-error">
        <div class="img">
          <img class="auto-img" :src="require('../../assets/images/order.png')" alt srcset />
        </div>
        <div class="info">
          <div class="title">您还没有相关的订单</div>
          <div class="sub">可以去看看有哪些想买的</div>
          <div class="btn">
            <button @click="goPage('Category')">随便逛逛</button>
          </div>
        </div>
      </div>
    </div>
    <div v-else class="orderlists" v-for="(item,index) in lists" :key="index">
      <van-cell
        center
        :title="'订单：'+item.orderId"
        :value="typeList[item.type]"
        :label="String(item.dateTime)"
      />
      <div class="card clearfix" v-for="(value,key) in item.goodsList" :key="key">
        <div class="picture fl">
          <img
            v-if="value.goods_small_logo == ''"
            class="auto-img"
            :src="require('../../assets/images/kzt.png')"
            alt
            srcset
          />
          <img v-else class="auto-img" :src="value.goods_small_logo" />
        </div>
        <div class="details fl">
          <div class="name">{{value.goods_name}}</div>
          <div class="type">{{value.typeName}}</div>
          <div class="price">￥{{value.goods_price}}</div>
          <div class="num fl">x{{value.num}}</div>
        </div>
      </div>
      <div class="orderfooter">
        <!-- 待支付 -->
        <div v-if="item.type == 1">
          <van-button size="mini" type="primary" @click="onPay(item.orderId)">支付订单</van-button>
          <van-button size="mini" type="danger" @click="onDel(index)">删除订单</van-button>
        </div>
        <!-- 待收货 -->
        <div v-else-if="item.type == 2">
          <van-button size="mini" type="info" @click="onGoods(item.orderId)">已收货</van-button>
        </div>
        <!-- 已完成 -->
        <div v-else-if="item.type == 3"></div>
      </div>
    </div>
  </div>
</template>

<script>
import { Toast } from "vant";

export default {
  created() {
    this.lists = this.$store.state.order.lists;
    this.typeList = this.$store.state.order.typeList;
  },
  methods: {
    goPage(name) {
      this.$router.push({ name });
    },
    onPay(orderId) {
      let index = this.lists.findIndex((item) => item.orderId == orderId);
      this.lists[index].type = 2; //切换支付后收货类型，调用支付接口
    },
    onDel(index) {
      this.lists[index].type = 6; //修改一个没有类型删除
      Toast("删除订单成功");
    },
    onGoods(orderId) {
      let index = this.lists.findIndex((item) => item.orderId == orderId);
      this.lists[index].type = 3; //切换待收货状态，
      Toast("收货成功");
    },
  },
  // 组件销毁
  beforeDestroy() {
    if (this.lists.length) {
      this.$store.commit("order/editId", this.lists);
    }
  },

};
</script>

<style lang="less" scoped>
.orderAll{
  // 空状态
  .Emptystate {
    width: 100%;
    height: 350px;
    margin-top: 10px;
    text-align: center;
    .o-t-error {
      margin: 0 auto;
      padding: 70px 0;
      .img {
        width: 150px;
        height: 150px;
        margin: 0 auto;
        img {
          margin: 0 auto;
          padding: 27px 0;
          width: 100%;
        }
      }
      .info {
        margin-top: 37px;
        .title {
          font-size: 18px;
        }
        .sub {
          margin: 4px 0;
          color: #999;
        }
        .btn {
          margin-top: 15px;
          button {
            background-image: linear-gradient(-90deg, #75deee, #3296f8);
            border-radius: 18px;
            border: 0;
            font-size: 15px;
            padding: 8px 20px;
            color: white;
          }
        }
      }
    }
  }
  .orderlists {
    width: 90%;
    padding: 2.5%;
    margin: 2.5%;
    margin-top: 10px;
    border-radius: 12px;
    border: 1px solid #e0e0e0;
    box-shadow: 4px 4px 6px #c9c9c9;
    .card {
      padding: 5px;
      margin: 5px;
      .picture {
        width: 100px;
        height: 100px;
      }
      .details {
        width: calc(~"100% - 105px");
        height: 100%;
        margin-left: 5px;
        .name {
          font-size: 15px;
          height: 40px;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
        }
        .type {
          margin: 5px 0;
          font-size: 16px;
          color: #999999;
        }
        .price {
          width: 50%;
          height: 28px;
          line-height: 28px;
          font-size: 16px;
          font-weight: 600;
          color: #eb5211;
          float: left;
        }
        .num {
          width: 47%;
          height: 28px;
          line-height: 28px;
          padding-right: 3%;
          text-align: right;
          font-size: 15px;
          color: #999999;
        }
      }
    }
    .orderfooter {
      text-align: right;
      padding: 0px 16px 10px 16px;
    }
  }
}
</style>